.GameDetail {
    width: 100%;
    min-height: 100vh;
    overflow-y: scroll;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 5;

    &::-webkit-scrollbar {
        width: 0.5rem;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #ff7676;
    }
    &::-webkit-scrollbar-track {
        background-color: #fdd;
    }

    scrollbar-color: #ff7676 #fdd;
    scrollbar-width: thin;

    .detail {
        width: 80%;
        min-height: 90vh;
        border-radius: 1rem;
        padding: 2rem 5rem;
        background-color: white;
        position: absolute;
        left: 10%;
        color: black;
        z-index: 10;

        img {
            width: 100%;
        }

        .stats {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .rating {
                img {
                    display: inline;
                    width: 2rem;
                }
            }

            .info {
                text-align: center;

                .platforms {
                    display: flex;
                    justify-content: space-evenly;

                    img {
                        max-width: 3rem;
                        margin-left: 3rem;

                        &:first-child {
                            margin-left: 0;
                        }
                    }
                }
            }
        }

        .media {
            margin-top: 5rem;

            img {
                width: 100%;
            }
        }

        .description {
            margin: 5rem 0;
            text-align: justify;
        }
    }
}

@media (max-width: 700px) {
    .GameDetail {
        .detail {
            padding: 2rem 3rem;

            .media {
                margin-top: 3rem;
            }

            .description {
                margin: 3rem 0;
            }
        }
    }
}

@media (max-width: 500px) {
    .GameDetail {
        .detail {
            padding: 2rem;
            .stats {
                .rating {
                    img {
                        width: 1.5rem;
                    }
                }
                .info {
                    .platforms {
                        img {
                            width: 1.5rem;
                            margin-left: 1.5rem;
                        }
                    }
                }
            }
        }
    }
}
